import React, { Component } from "react";
import photo from "../rsz_photo5.jpg";
import {
  Container,
  Segment,
  Grid,
  Image,
  Header,
  Divider,
  List,
  Icon,
  Responsive,
} from "semantic-ui-react";

class Landing extends Component {
  state = {};
  render() {
    return (
      <div>
        <Responsive minWidth={Responsive.onlyTablet.minWidth}>
          <Container>
            {/* Header Page */}
            <Divider hidden section />
            <Segment basic textAlign="center">
              <Grid columns={3}>
                <Grid.Column>
                  <Image
                    size="small"
                    src={photo}
                    inline
                    rounded
                    circular
                  ></Image>
                </Grid.Column>
                <Grid.Column textAlign="left" verticalAlign="middle">
                  <Header
                    size="huge"
                    color="grey"
                    content="Cacuk Jati Pangestu"
                  />
                  <p>Bandung, Indonesia </p>
                  <a href="http://caacuk.github.io">
                    <Icon name="github" size="large" color="grey" />
                  </a>
                  <a href="http://www.instagram.com/caacuk">
                    <Icon name="instagram" size="large" color="grey" />
                  </a>
                </Grid.Column>
              </Grid>
            </Segment>
            <Divider hidden section />

            {/* Content */}
            <Grid columns={2} textAlign="left">
              <Grid.Row>
                <Grid.Column textAlign="justified" width="3">
                  <Segment basic>
                    <Header color="grey" content="About me" />
                  </Segment>
                </Grid.Column>
                <Grid.Column textAlign="justified" width="11">
                  <p style={{ fontSize: 17 }}>
                    I am motivated to be a software engineer and learn new
                    knowledge. <br />
                    Graduated with a degree in Computer Science from Universitas
                    Pendidikan Indonesia (UPI).
                  </p>
                </Grid.Column>
              </Grid.Row>
              <Grid.Row>
                <Grid.Column textAlign="justified" width="3">
                  <Segment basic>
                    <Header color="grey" content="Skills" />
                  </Segment>
                </Grid.Column>
                <Grid.Column textAlign="left">
                  <List as="ol" size="large">
                    <List.Item as="li" value="-">
                      Node.js
                    </List.Item>
                    <List.Item as="li" value="-">
                      React.js
                    </List.Item>
                    <List.Item as="li" value="-">
                      Express.js
                    </List.Item>
                    <List.Item as="li" value="-">
                      Flask Python
                    </List.Item>
                    <List.Item as="li" value="-">
                      MySQL
                    </List.Item>
                    <List.Item as="li" value="-">
                      C/C++
                    </List.Item>
                  </List>
                </Grid.Column>
              </Grid.Row>
              <Grid.Row>
                <Grid.Column textAlign="justified" width="3">
                  <Segment basic>
                    <Header color="grey" content="Education" />
                  </Segment>
                </Grid.Column>
                <Grid.Column textAlign="left">
                  <Header size="small">
                    Universitas Pendidikan Indonesia (UPI) (2016-2020)
                  </Header>
                  <p>
                    <i>Bachelor of Computer Science - Cum Laude</i>
                  </p>
                  <Header size="small">SMAN 24 Bandung (2013-2016)</Header>
                  <p>
                    <i>Science Major</i>
                  </p>
                </Grid.Column>
              </Grid.Row>
            </Grid>
          </Container>
        </Responsive>

        <Responsive maxWidth={Responsive.onlyMobile.maxWidth}>
          <Container>
            {/* Header Page */}
            <Divider hidden section />
            <Segment basic textAlign="center">
              <Grid columns={3}>
                <Grid.Column>
                  <Image
                    size="small"
                    src={photo}
                    inline
                    rounded
                    circular
                  ></Image>
                </Grid.Column>
                <Grid.Column textAlign="left" verticalAlign="middle">
                  <Header
                    size="medium"
                    color="grey"
                    content="Cacuk Jati Pangestu"
                  />
                  <p style={{ fontSize: 10 }}>Bandung, Indonesia </p>
                  <a href="http://caacuk.github.io">
                    <Icon name="github" size="large" color="grey" />
                  </a>
                  <a href="http://www.instagram.com/caacuk">
                    <Icon name="instagram" size="large" color="grey" />
                  </a>
                </Grid.Column>
              </Grid>
            </Segment>
            <Divider hidden section />

            {/* Content */}
            <Grid columns={2} textAlign="left">
              <Grid.Row>
                <Grid.Column textAlign="justified" width="5">
                  <Segment basic>
                    <Header color="grey" content="About me" size="small" />
                  </Segment>
                </Grid.Column>
                <Grid.Column textAlign="justified" width="11">
                  <p style={{ fontSize: 14 }}>
                    I am motivated to be a software engineer and learn new
                    knowledge. <br />
                    Graduated with a degree in Computer Science from Universitas
                    Pendidikan Indonesia (UPI).
                  </p>
                </Grid.Column>
              </Grid.Row>
              <Grid.Row>
                <Grid.Column textAlign="justified" width="5">
                  <Segment basic>
                    <Header color="grey" content="Skills" size="small" />
                  </Segment>
                </Grid.Column>
                <Grid.Column textAlign="left">
                  <List as="ol" size="small">
                    <List.Item as="li" value="-">
                      Node.js
                    </List.Item>
                    <List.Item as="li" value="-">
                      React.js
                    </List.Item>
                    <List.Item as="li" value="-">
                      Express.js
                    </List.Item>
                    <List.Item as="li" value="-">
                      Flask Python
                    </List.Item>
                    <List.Item as="li" value="-">
                      MySQL
                    </List.Item>
                    <List.Item as="li" value="-">
                      C/C++
                    </List.Item>
                  </List>
                </Grid.Column>
              </Grid.Row>
              <Grid.Row>
                <Grid.Column textAlign="justified" width="5">
                  <Segment basic>
                    <Header color="grey" content="Education" size="small" />
                  </Segment>
                </Grid.Column>
                <Grid.Column textAlign="left">
                  <Header size="small">
                    Universitas Pendidikan Indonesia (UPI) (2016-2020)
                  </Header>
                  <p>
                    <i>Bachelor of Computer Science - Cum Laude</i>
                  </p>
                  <Header size="small">SMAN 24 Bandung (2013-2016)</Header>
                  <p>
                    <i>Science Major</i>
                  </p>
                </Grid.Column>
              </Grid.Row>
            </Grid>
          </Container>
        </Responsive>
      </div>
    );
  }
}

export default Landing;
