import React, { Component } from "react";
import {
  Container,
  Form,
  Input,
  Label,
  Icon,
  Divider,
  Segment,
  Button,
  Grid,
  Header,
  Message,
} from "semantic-ui-react";

class Login extends Component {
  constructor() {
    super();
    this.state = {
      email: "",
      password: "",
    };
  }

  onChange = (e) => {
    const { name, value } = e.target;
    // [name] = "email" or [name] = "password"
    this.setState({ [name]: value });
  };

  onSubmit = (e) => {
    e.preventDefault();

    const user = {
      email: this.state.email,
      password: this.state.password,
    };
    console.log(user);
  };

  render() {
    return (
      <Container>
        <Grid
          textAlign="center"
          style={{ height: "100vh" }}
          verticalAlign="top"
        >
          <Grid.Column style={{ maxWidth: 450 }}>
            <Segment raised textAlign="center" stacked>
              <Header size="large">Login</Header>
              <Form size="large" onSubmit={this.onSubmit}>
                <Form.Input
                  name="email"
                  icon="user"
                  value={this.state.email}
                  onChange={this.onChange}
                  iconPosition="left"
                  placeholder="E-mail Address"
                ></Form.Input>
                <Form.Input
                  name="password"
                  icon="lock"
                  value={this.state.password}
                  onChange={this.onChange}
                  iconPosition="left"
                  placeholder="Password"
                  type="password"
                ></Form.Input>
                <Button color="teal" size="large" fluid>
                  Login
                </Button>
              </Form>
            </Segment>
            <Message>
              New to us?
              <a href="#"> Sign Up</a>
            </Message>
          </Grid.Column>
        </Grid>
      </Container>
    );
  }
}

export default Login;
